<template>
	<div class="c4w-section" type="flex" justify="center">
		<div class="container-md">
			<a-row>
				<a-col>
					<h1 v-if="section.title">{{ section.title }}</h1>
					<p v-if="section.description">{{ section.description }} ></p>
				</a-col>

				<template v-if="!hasJobs">
					<a-divider></a-divider>

					<a-result title="No jobs at the moment! Check back soon!">
						<template #icon>
							<img width="100%" src="images/helper/pablo-come-back-later.png" alt="Come Back Soon" />
						</template>
						<!-- <template #extra>
							<a-button>
								Next
							</a-button>
						</template> -->
					</a-result>
				</template>
				<template v-else>
					<template v-for="(jobsArr, category) in jobs">
						<a-col v-if="jobsArr.length > 0" :key="category">
							<a-divider></a-divider>
							<p :style="{ color: '#c25959', marginBottom: '0rem' }">
								{{ jobLabels.categories[category] }}
							</p>
							<a-collapse>
								<a-icon
									slot="expandIcon"
									:style="{ fontSize: '1rem' }"
									two-tone-color="#C25959"
									type="plus"
								></a-icon>
								<a-collapse-panel v-for="(info, i) in jobsArr" :key="i" :header="info.job_title">
									<a-row :gutter="[0, 20]">
										<a-col>
											<p>
												<a-tag color="pink">{{ jobLabels.types[info.job_type] }}</a-tag>
												<a-tag color="cyan">
													<a-icon type="environment"></a-icon>
													{{ jobLabels.locations[info.job_location] }}
												</a-tag>
											</p>
										</a-col>
										<a-col v-if="info.job_description">
											<p v-html="info.job_description"></p>
										</a-col>
										<a-col v-if="info.job_responsibilities">
											<h4>Responsibilities</h4>
											<p v-html="info.job_responsibilities"></p>
										</a-col>
										<a-col v-if="info.job_requirements">
											<h4>Requirements</h4>
											<p v-html="info.job_requirements"></p>
										</a-col>
										<a-col v-if="info.job_benefits">
											<h4>Beneifts</h4>
											<p v-html="info.job_benefits"></p>
										</a-col>

										<a-col>
											<a-button :disabled="!info.job_link" :href="info.job_link">
												Apply now
												<a-icon type="arrow-right"></a-icon>
											</a-button>
										</a-col>
									</a-row>
								</a-collapse-panel>
							</a-collapse>
						</a-col>
					</template>
					<!-- <a-divider></a-divider> -->
				</template>
			</a-row>
		</div>
	</div>
</template>

<script>
export default {
	data() {
		return {
			hasJobs: false,
			section: {
				title: "Job Board",
				description2:
					"We are a safe digital community for women to share and learn from each others experiences. We" +
					"\tprovide workshops for organizations who wish to practice Diversity, Equity and Inclusion in" +
					"\ttheir work culture. We also provide virtual mentorship for women. Lastly, we are not against" +
					"\tfeminism rather we are working towards eliminating the need for feminists. We are in the 21st" +
					"\tcentury, we need to think forward, appreciate the efforts the society has made towards" +
					"\tpracticing equality and educate the future."
			},
			jobs: {
				engineering: [],
				design: [],
				marketing: []
			},
			jobLabels: {
				locations: {
					"austin-texas": "Austin, TX",
					remote: "Remote",
					"ames-ia": "Ames, IA"
				},
				types: {
					"part-time": "Part Time",
					"full-time": "Full Time",
					temporary: "Temporary",
					volunteer: "Volunteer",
					contract: "Contract",
					freelance: "Freelance",
					internship: "Internship"
				},
				categories: {
					design: "Design",
					engineering: "Engineering",
					marketing: "Marketing"
				}
			},
			errorMessage: null
		};
	},
	beforeMount() {
		this.$client
			.getItems("jobs")
			.then((res) => {
				const jobsArr = res.data;

				if (jobsArr.length > 0) {
					this.hasJobs = true;

					// sort jobs based on job_category
					jobsArr.map((job) => {
						this.jobs[job.job_category].push(job);
					});
				}
			})
			.catch((error) => {
				this.errorMessage = error.message;
			});
	}
};
</script>
